import React from "react";
import { Grid, WhiteSpace, WingBlank } from "antd-mobile";
import { withRouter } from "react-router-dom";
import { hot } from "react-hot-loader";
import { MainMenuList } from "./helpers/constants";
import { compose } from "@vxmind/vxhelpers";

export function IndexPage({ history }: any) {
    const handleNavigate = ({ route }: any) => {
        if (route) {
            history.push(route);
        }
    };

    return (
        <div>
            <img
                src={require("./images/e政工首页.jpg")}
                alt="指尖党务"
                style={{ width: "100%" }}
            />
            <WhiteSpace size="xs" />
            <WingBlank size="sm">
                <Grid
                    data={MainMenuList}
                    columnNum={3}
                    activeStyle={false}
                    onClick={handleNavigate}
                />

                <br/>
            </WingBlank>
        </div>
    );
}

export default compose(
    hot(module),
    withRouter
)(IndexPage);
